import { InAppBrowser } from '@ionic-native/in-app-browser';
import { AngularFireAuth } from 'angularfire2/auth';
import { AuthProvider } from './../../providers/auth/auth';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, AlertController, LoadingController } from 'ionic-angular';

@IonicPage()
@Component({
  selector: 'page-signup',
  templateUrl: 'signup.html',
})
export class SignupPage {
  matric: string;
  password: string;
  phone: number;

  profile = {
    matric: this.matric,
    password: this.password,
    phone: this.phone
  }
 
  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    public authprovider: AuthProvider,
    public fire: AngularFireAuth,
    public alertCtrl: AlertController,
    public loadingCtrl: LoadingController,
    public iab:InAppBrowser
  ) {

  }

  signup() {
    let loader = this.loadingCtrl.create({
      content: "Please wait...",
    });
    loader.present();
    

    this.authprovider.signup(this.profile).then(() => {
      this.fire.auth.currentUser.sendEmailVerification().then(() => {
        this.navCtrl.push('VerifymailPage', { 'profile': this.profile })
        loader.dismiss();
        // let alert = this.alertCtrl.create({
        //   title:'Congratulation!',
        //   subTitle: 'We have sent an email to '+this.profile.matric +'@student.upm.edu.my. Please check your UPM student official email and get verify as it mandatory to all students.',
        //   buttons: [
        //     {
        //       text: 'OK',
        //       role: 'cancel',
        //       handler: () => {
        //         console.log('Cancel clicked');
        //       }
        //     },
        //     {
        //       text: 'Check Email',
        //       handler: () => {
        //         this.iab.create('http://mail.student.upm.edu.my/', '_system')
        //       }
        //     }
        //   ]
        // });
        // alert.present();
      })
    })
  //     .catch(error => {
  //     let alert = this.alertCtrl.create({
  //       subTitle: error.message,
  //       buttons: ['OK']
  //     });
  //     alert.present();
  //     loader.dismiss();
  //  })
  }

  terms() {
    this.navCtrl.push('TermsPage')
  }

}



